import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import {
  InputDate,
  ReanderSelect,
} from "../../../../components/helpers/field";
import { getTglSystem } from "../../../../components/helpers/function";
import ValidasiLaporanKeuangan from "../../../../Validasi/LaporanKeuangan/ValidasiLaporanKeuangan";
class HeadLaporanKeuanganKas extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off"  onKeyPress={(e) => {
        e.key === "Enter" && e.preventDefault();
      }}>
        <div className="row">
          <div className="col-lg-4">
          <Field
            name="tgl_awal"
            component={InputDate}
            label="Tanggal Dari"
            type="text"
            selected={this.state.tgl_awal}
            onChange={(date) => this.setStartDate(date)}
            placeholder="Masukan Tanggal Dari"
          />
          </div>
          <div className="col-lg-4">
            <Field
              name="type"
              component={ReanderSelect}
              onChange={(e)=>this.props.onChange(e)}  
              options={[
                { value: "detail", name: "Detail" },
                { value: "rekap", name: "Rekap" },
              ]}
              label="Type"
              placeholder="Pilih Type"
            />
          </div>
          
          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button type="submit" value="PDF" name="type_btn" className="btn btn-primary btn-block">
              {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

HeadLaporanKeuanganKas = reduxForm({
  form: "HeadLaporanKeuanganKas",
  enableReinitialize: true,
  validate: ValidasiLaporanKeuangan,
})(HeadLaporanKeuanganKas);
// export default connect()(HeadLaporanKeuanganKas);
export default connect((state) => {
  return {
    datasales: state.datamaster.getDataSales,
    initialValues: {
      tgl_awal: getTglSystem(),
    },
  };
})(HeadLaporanKeuanganKas);
